import React, { Component, Fragment } from 'react'
import { Link as LINK, Route } from 'react-router-dom'
import { createStyles, Theme, makeStyles } from '@material-ui/core/styles';
import {AppBar, Typography, Toolbar, Link, Button} from '@material-ui/core'


const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    content: {
      flexGrow: 1,
      padding: theme.spacing(3),
    },
    // necessary for content to be below app bar
    toolbar: theme.mixins.toolbar,
  }),
);

  const MyCursos = (props: IProps) => {
    const classes = useStyles();

    return(
        <Fragment>
            <main className={classes.content}>
                <div className={classes.toolbar} />
                {/* <Link to="/1">Dash 1</Link>
                <Link to="/2">Dash 2</Link> */}

                <h1>vista Cursos</h1> 
            </main>
        </Fragment>
    )
}

interface IProps{

}

export default MyCursos