import React, { Component, Fragment, useState } from 'react'
import { Link as LINK, Route } from 'react-router-dom'
import { createStyles, Theme, makeStyles, withStyles, WithStyles  } from '@material-ui/core/styles';
import {CssBaseline, Typography, Container, Button} from '@material-ui/core'
import {Box, Dialog, Link, MenuItem , FormControlLabel, TextField, Avatar } from '@material-ui/core';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined'
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import MuiDialogContent from '@material-ui/core/DialogContent';
import MuiDialogActions from '@material-ui/core/DialogActions';

function Copyright() {
    return (
      <Typography variant="body2" color="textSecondary" align="center">
        {'Copyright © '}
        <Link color="inherit" href="https://material-ui.com/">
          Dash Code Website
        </Link>{' '}
        {new Date().getFullYear()}
        {'.'}
      </Typography>
    );
  }
  const styles = (theme: Theme) =>
  createStyles({
    root: {
      margin: 0,
      padding: theme.spacing(2),
    },
    closeButton: {
      position: 'absolute',
      right: theme.spacing(1),
      top: theme.spacing(1),
      color: theme.palette.grey[500],
    },
  });
  const useStyles = makeStyles((theme) => ({
        paper: {
        marginTop: theme.spacing(8),
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        },
        avatar: {
        margin: theme.spacing(1),
        backgroundColor: theme.palette.secondary.main,
        },
        form: {
        width: '100%', // Fix IE 11 issue.
        marginTop: theme.spacing(1),
        },
        submit: {
        margin: theme.spacing(3, 0, 2),
        },
    }));
  
  export interface DialogTitleProps extends WithStyles<typeof styles> {
    id: string;
    children: React.ReactNode;
    onClose: () => void;
  }
  
  const DialogTitle = withStyles(styles)((props: DialogTitleProps) => {
    const { children, classes, onClose, ...other } = props;
    return (
      <MuiDialogTitle disableTypography className={classes.root} {...other}>
        <Typography variant="h6">{children}</Typography>
        {onClose ? (
          <IconButton aria-label="close" className={classes.closeButton} onClick={onClose}>
            <CloseIcon />
          </IconButton>
        ) : null}
      </MuiDialogTitle>
    );
  });
  
  const DialogContent = withStyles((theme: Theme) => ({
    root: {
      padding: theme.spacing(2),
    },
  }))(MuiDialogContent);
  
  const DialogActions = withStyles((theme: Theme) => ({
    root: {
      margin: 0,
      padding: theme.spacing(1),
    },
  }))(MuiDialogActions);

  export default function MyRegistro() {
    const classes = useStyles();
    const [nombre, setNombre] = useState("");
    const [apellido, setApellido] = useState("");
    const [correo, setCorreo] = useState("");
    const [celular, setCelular] = useState("");
    const [open, setOpen] = React.useState(false);
    const [curso, setCurso] = React.useState('');

    const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
      setCurso(event.target.value);
    };
    const validateForm = () => (nombre.length > 0 && apellido.length > 0 && correo.length > 0 && celular.length > 0);
    const handleSubmit = async(event:any) => {
      event.preventDefault()
      setOpen(true)
    }

    const handleClickOpen = () => {
        setOpen(true);
    };
    const handleClose = () => {
        setOpen(false);
    };

    const cursos = [
      {
        value: 'Curso 1',
        label: 'Curso 1',
      },
      {
        value: 'Curso 2',
        label: 'Curso 2',
      },
      {
        value: 'Curso 3',
        label: 'Curso 3',
      },
      {
        value: 'Curso 4',
        label: 'Curso 4',
      },
      {
        value: 'Curso 5',
        label: 'Curso 5',
      },
      {
        value: 'Curso 6',
        label: 'Curso 6',
      },
      {
        value: 'Curso 7',
        label: 'Curso 7',
      },
      {
        value: 'Curso 8',
        label: 'Curso 8',
      },
      {
        value: 'Curso 9',
        label: 'Curso 9',
      },
      {
        value: 'Curso 10',
        label: 'Curso 10',
      }
    ];
    return (
      <Container component="main" maxWidth="xs">
        <CssBaseline />
        <div className={classes.paper}>
          <Avatar className={classes.avatar}>
            <LockOutlinedIcon />
          </Avatar>
          <Typography component="h1" variant="h5">
            Ingrese sus Datos
          </Typography>
          <form className={classes.form} onSubmit={handleSubmit}  noValidate>
            <TextField
              value={nombre}
              onChange={e => setNombre(e.target.value)}
              variant="outlined"
              margin="normal"
              required
              fullWidth
              id="nombre"
              label="Nombre"
              name="nombre"
              autoComplete="nombre"
              autoFocus
            />
            <TextField
              value={apellido}
              onChange={e => setApellido(e.target.value)}
              variant="outlined"
              margin="normal"
              required
              fullWidth
              name="apellido"
              label="Apellidos"
              id="apellido"
              autoComplete="apellido"
            />
            <TextField
              value={correo}
              onChange={e => setCorreo(e.target.value)}
              variant="outlined"
              margin="normal"
              required
              fullWidth
              name="correo"
              label="Correo"
              id="correo"
              autoComplete="correo"
            />
            <TextField
              value={celular}
              onChange={e => setCelular(e.target.value)}
              variant="outlined"
              margin="normal"
              required
              fullWidth
              name="celular"
              label="Celular"
              id="celular"
              autoComplete="celular"
            />
            <TextField
              value={curso}
              onChange={handleChange}
              variant="outlined"
              margin="normal"
              required
              fullWidth
              select
              label="Curso"
              id="outlined-select-currency"
              helperText="Porfavor seleccione un curso"
              
            >
              {cursos.map((option) => (
                <MenuItem key={option.value} value={option.value}>
                  {option.label}
                </MenuItem>
              ))}
            </TextField>

            <Button
              disabled={!validateForm()}
              type="submit"
              fullWidth
              variant="contained"
              color="primary"
              className={classes.submit}
            >
              Enviar Datos
            </Button>
          </form>
        </div>
        <Box mt={8}>
          <Copyright />
        </Box>
        <Dialog onClose={handleClose} aria-labelledby="customized-dialog-title" open={open}>
            <DialogTitle id="customized-dialog-title" onClose={handleClose}>Gracias por su confianza</DialogTitle>
            <DialogContent dividers>
            <Typography gutterBottom>
                Gracias por visitarnos. En un tiempo se le mandará sus credenciales al correo: {correo}
            </Typography>
            </DialogContent>
            <DialogActions>
            
            <Link component={LINK} to="/">
            <Button autoFocus onClick={handleClose} color="primary">
                Ir al Menu
            </Button>
            </Link>
            </DialogActions>
        </Dialog>
      </Container>
    );
  }