const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    content: {
      flexGrow: 1,
      padding: theme.spacing(3),
    },
    // necessary for content to be below app bar
    toolbar: theme.mixins.toolbar,
    root: {
      // maxWidth: 400,
      flexGrow: 1,
    },
    header: {
      display: 'flex',
      alignItems: 'center',
      height: 50,
      paddingLeft: theme.spacing(4),
      backgroundColor: theme.palette.background.default,
    },
    img: {
      // height: 255,
      height: '775px',
      display: 'block',
      // maxWidth: 400,
      overflow: 'hidden',
      width: '100%',
    },
    heading: {
      fontSize: theme.typography.pxToRem(15),
      fontWeight: theme.typography.fontWeightRegular,
    },
    footer: {
      backgroundColor: red[500],
      // backgroundColor: theme.palette.background.paper,
      padding: theme.spacing(6),
    },
  }),
);

function Copyright() {
  return (
    <Typography variant="body2" color="textSecondary" align="center">
      {'Copyright © '}
      <Link color="inherit" href="">
        Dash Code Website
      </Link>{' '}
      {new Date().getFullYear()}
      {'.'}
    </Typography>
  );
}

  const MyContent = (props: IProps) => {
    const classes = useStyles();
    const theme = useTheme();
    const [activeStep, setActiveStep] = React.useState(0);
    const maxSteps = tutorialSteps.length;

    const handleNext = () => {
      setActiveStep((prevActiveStep) => prevActiveStep + 1);
    };
  
    const handleBack = () => {
      setActiveStep((prevActiveStep) => prevActiveStep - 1);
    };
  
    const handleStepChange = (step: number) => {
      setActiveStep(step);
    };


    return(
        <Fragment>
            <main className={classes.content}>
            <div className={classes.root}>
              {/* <Paper square elevation={0} className={classes.header}>
                <Typography>{tutorialSteps[activeStep].label}</Typography>
              </Paper> */}
              <AutoPlaySwipeableViews
                axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
                index={activeStep}
                onChangeIndex={handleStepChange}
                enableMouseEvents
              >
                {tutorialSteps.map((step, index) => (
                  <div key={step.label}>
                    {Math.abs(activeStep - index) <= 2 ? (
                      <img className={classes.img} src={step.imgPath} alt={step.label} />
                    ) : null}
                  </div>
                ))}
              </AutoPlaySwipeableViews>
              <MobileStepper
                steps={maxSteps}
                position="static"
                variant="text"
                activeStep={activeStep}
                nextButton={
                  <Button size="small" onClick={handleNext} disabled={activeStep === maxSteps - 1}>
                    Next
                    {theme.direction === 'rtl' ? <KeyboardArrowLeft /> : <KeyboardArrowRight />}
                  </Button>
                }
                backButton={
                  <Button size="small" onClick={handleBack} disabled={activeStep === 0}>
                    {theme.direction === 'rtl' ? <KeyboardArrowRight /> : <KeyboardArrowLeft />}
                    Back
                  </Button>
                }
              />
            </div>
            <div className={classes.root}>
              <Grid container spacing={3}>
                {
                  modulos.map( modulo => (
                    <Grid item xs={4}>
                      <ExpansionPanel>
                        <ExpansionPanelSummary
                          expandIcon={<ExpandMoreIcon />}
                          aria-controls="panel1a-content"
                          id="panel1a-header">
                          <Typography className={classes.heading}>{modulo.nombre}</Typography>
                        </ExpansionPanelSummary>
                        <ExpansionPanelDetails>
                          <Typography>
                            {modulo.detalle}
                          </Typography>
                        </ExpansionPanelDetails>
                        {
                          modulo.cursos.map( curso => (
                            <div>
                              <ExpansionPanel>
                                <ExpansionPanelSummary
                                  expandIcon={<ExpandMoreIcon />}
                                  aria-controls="panel1a-content"
                                  id="panel1a-header">
                                  <Typography className={classes.heading}>{curso.nombre}</Typography>
                                </ExpansionPanelSummary>
                                {
                                  curso.temas.map( tema => (
                                    <div>
                                      <ExpansionPanelDetails>
                                        <Typography>
                                          {tema.nombre}
                                        </Typography>
                                      </ExpansionPanelDetails>
                                      <ul>
                                      {
                                        tema.list?.map( data => (
                                        <li style={{marginLeft: "35px"}}>{data.nombre}</li>
                                        ))
                                      }
                                      </ul>
                                      
                                    </div>
                                    
                                  ))
                                }
                              </ExpansionPanel>
                            </div>
                          ))
                        }
                        <ExpansionPanelDetails>
                          <Typography>
                            {modulo.total}
                          </Typography>
                        </ExpansionPanelDetails>              
                      </ExpansionPanel>
                    </Grid>
                  ))
                }
              </Grid>
            </div>
            </main>

            <footer className={classes.footer} >
              <Typography variant="h6" align="center" gutterBottom>
                Contactanos a través del número: 941987670
              </Typography>
              <Typography variant="subtitle1" align="center" color="textSecondary" component="p">
                Calle Blandet 217, oficina 202, San Isidro 
              </Typography>
              <Copyright />
            </footer>
        </Fragment>
    )
}

interface IProps{

}

export default MyContent