import React, { Component, Fragment } from 'react'
import { Link as LINK, Route } from 'react-router-dom'
import { createStyles, Theme, makeStyles } from '@material-ui/core/styles';
import {AppBar, Typography, Toolbar, Link, Button} from '@material-ui/core'



const useStyles = makeStyles((theme: Theme) => ({
    '@global': {
      ul: {
        margin: 0,
        padding: 0,
        listStyle: 'none',
      },
    },
    appBar: {
      borderBottom: `1px solid ${theme.palette.divider}`,
    },
    toolbar: {
      flexWrap: 'wrap',
    },
    toolbarTitle: {
      flexGrow: 1,
    },
    link: {
      margin: theme.spacing(1, 1.5),
    },
    heroContent: {
      padding: theme.spacing(8, 0, 6),
    },
    cardHeader: {
      backgroundColor:
        theme.palette.type === 'light' ? theme.palette.grey[200] : theme.palette.grey[700],
    },
    cardPricing: {
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'baseline',
      marginBottom: theme.spacing(2),
    },
    footer: {
      borderTop: `1px solid ${theme.palette.divider}`,
      marginTop: theme.spacing(8),
      paddingTop: theme.spacing(3),
      paddingBottom: theme.spacing(3),
      [theme.breakpoints.up('sm')]: {
        paddingTop: theme.spacing(6),
        paddingBottom: theme.spacing(6),
      },
    },
  }));

const MyToolBar = (props: IProps) => {
    const classes = useStyles();

    return(
        <Fragment>
            <AppBar position="static" color="default" elevation={0} className={classes.appBar}>
                <Toolbar className={classes.toolbar}>
                    <Typography variant="h6" color="inherit" noWrap className={classes.toolbarTitle}>
                    <img src={"data:image/jpeg;base64,"} alt={"app"} />
                    </Typography>
                    {/* <img src={"https://lh3.googleusercontent.com/proxy/M4-Asj-u6Rv7BZut9GGOuhS_QN4Kj-f1G5axfxCuFI0rCAnVgy5iUQpxdjVLjFlJsEMMaCkflJuKf1f6S1zf5AI4AuD03dg6Zd8HaX2hKC_3VA"} alt={"app"} /> */}
                    <nav>
                        <Link variant="button" color="textPrimary" component={LINK} to="/registrate" className={classes.link}>
                            Registrate
                        </Link>
                        <Link variant="button" color="textPrimary"  href="http://34.66.25.209/moodle/login/index.php" target="_blank" className={classes.link}>
                            Mi Aula Virtual
                        </Link>
                        
                    </nav>
                </Toolbar>
            </AppBar>
        </Fragment>
    )
}

interface IProps{

}

export default MyToolBar
